// Components styles.
@import "components/_footer-nav.scss";
@import "components/_front.scss";
@import "components/_menu.scss";
@import "components/_mobile.scss";
@import "components/_tablet.scss";
@import "components/_top-nav.scss";

// Default variables.
@import "variables";

// Import mixins
@import "mixins/_breakpoints.scss";
@import "mixins/_mixins.scss";

* {
  font-family: "Josefin Sans", sans-serif;
  margin: 0;
  .site-wide-page,
  .front-wrapper a {
    text-decoration: none;
    &:visited {
      color: #000;
    }
    &:hover {
      color: $green;
    }
  }
}
.front-wrapper {
  max-width: 100%;
  margin: 0 auto;
}
